"use client"

import { useState, useEffect } from 'react'

export default function Header() {
  const [isMenuOpen, setIsMenuOpen] = useState(false)
  const [isScrolled, setIsScrolled] = useState(false)
  const [isLoaded, setIsLoaded] = useState(false)

  useEffect(() => {
    // Trigger intro animation on mount
    setIsLoaded(true)

    // Handle scroll effect for glass morphism
    const handleScroll = () => {
      setIsScrolled(window.scrollY > 20)
    }

    window.addEventListener('scroll', handleScroll)
    return () => window.removeEventListener('scroll', handleScroll)
  }, [])

  return (
    <header 
      className={`fixed top-0 left-0 right-0 z-50 transition-all duration-500 ease-out ${
        isScrolled 
          ? 'bg-white/60 backdrop-blur-sm border-b border-white/10 shadow-lg' 
          : 'bg-white/90 backdrop-blur-sm border-b border-gray-200'
      } ${isLoaded ? 'translate-y-0 opacity-100' : '-translate-y-full opacity-0'}`}
    >
      <div className="container mx-auto px-4 sm:px-6 lg:px-8">
        <div className="flex justify-between items-center h-16">
          {/* Logo with intro animation */}
          <div className="flex-shrink-0">
            <a 
              href="/" 
              className={`text-2xl font-bold text-black tracking-tight transition-all duration-700 ease-out ${
                isLoaded 
                  ? 'translate-x-0 opacity-100' 
                  : '-translate-x-8 opacity-0'
              } hover:scale-105 transform`}
              style={{ transitionDelay: '200ms' }}
            >
              RXGO
            </a>
          </div>

          {/* Desktop Navigation with staggered animations */}
          <nav className="hidden md:flex space-x-8">
            {[
              { href: '/', label: 'Home', delay: '300ms' },
              { href: '/shop', label: 'Shop', delay: '400ms' },
              { href: '/about', label: 'About', delay: '500ms' },
              { href: '/contact', label: 'Contact', delay: '600ms' }
            ].map((item, index) => (
              <a 
                key={item.href}
                href={item.href}
                className={`text-gray-900 hover:text-gray-600 px-3 py-2 text-sm font-medium transition-all duration-700 ease-out hover:scale-105 transform ${
                  isLoaded 
                    ? 'translate-y-0 opacity-100' 
                    : '-translate-y-4 opacity-0'
                }`}
                style={{ transitionDelay: item.delay }}
              >
                {item.label}
              </a>
            ))}
          </nav>

          {/* Mobile menu button with animation */}
          <div className="md:hidden">
            <button
              onClick={() => setIsMenuOpen(!isMenuOpen)}
              className={`text-gray-900 hover:text-gray-600 focus:outline-none focus:text-gray-600 transition-all duration-700 ease-out hover:scale-110 transform ${
                isLoaded 
                  ? 'translate-x-0 opacity-100' 
                  : 'translate-x-8 opacity-0'
              }`}
              style={{ transitionDelay: '400ms' }}
              aria-label="Toggle menu"
            >
              <div className="w-6 h-6 flex flex-col justify-center items-center">
                <span className={`block h-0.5 w-6 bg-current transform transition-all duration-300 ease-in-out ${
                  isMenuOpen ? 'rotate-45 translate-y-1.5' : ''
                }`}></span>
                <span className={`block h-0.5 w-6 bg-current transform transition-all duration-300 ease-in-out ${
                  isMenuOpen ? 'opacity-0' : 'mt-1'
                }`}></span>
                <span className={`block h-0.5 w-6 bg-current transform transition-all duration-300 ease-in-out ${
                  isMenuOpen ? '-rotate-45 -translate-y-1.5' : 'mt-1'
                }`}></span>
              </div>
            </button>
          </div>
        </div>

        {/* Mobile Navigation with glass effect and animations */}
        {isMenuOpen && (
          <div className="md:hidden animate-in slide-in-from-top-2 duration-300">
            <div className={`px-2 pt-2 pb-3 space-y-1 border-t border-white/10 ${
              isScrolled 
                ? 'bg-white/70 backdrop-blur-sm' 
                : 'bg-white/90 backdrop-blur-sm'
            }`}>
              {[
                { href: '/', label: 'Home' },
                { href: '/shop', label: 'Shop' },
                { href: '/about', label: 'About' },
                { href: '/contact', label: 'Contact' }
              ].map((item, index) => (
                <a 
                  key={item.href}
                  href={item.href}
                  className="block px-3 py-2 text-base font-medium text-gray-900 hover:text-gray-600 transition-all duration-200 hover:bg-white/50 rounded-md transform hover:translate-x-1"
                  style={{ 
                    animationDelay: `${index * 50}ms`,
                    animation: 'fadeInUp 0.3s ease-out forwards'
                  }}
                >
                  {item.label}
                </a>
              ))}
            </div>
          </div>
        )}
      </div>

      <style jsx>{`
        @keyframes fadeInUp {
          from {
            opacity: 0;
            transform: translateY(10px);
          }
          to {
            opacity: 1;
            transform: translateY(0);
          }
        }
      `}</style>
    </header>
  )
}
